document.addEventListener('DOMContentLoaded', function () {
    mobileMenu();
    setActiveMenuItem();
  });
  
  function mobileMenu() {
    const btnMenu = document.querySelector('.icon_menu');
    if (btnMenu) {
      btnMenu.addEventListener('click', function () {
        const menuItems = document.querySelector('.menu');
        menuItems.classList.toggle('active');
      });
    }
  }
  
  function setActiveMenuItem() {
    const activePage = location.pathname;
    const menuItems = document.querySelectorAll('a');
    menuItems.forEach(function (menuItem) {
      if (menuItem.getAttribute('href') === activePage) {
        menuItem.classList.add('activ');
      }
    });
  }
  
  // Redireccionar desde '/index.html' a '/'
  if (window.location.pathname === '/index.html') {
    window.location.replace('/');
  }
  
  // Redireccionar desde '/nosotros.html' a '/nosotros'
  if (window.location.pathname === '/nosotros.html') {
    window.location.replace('/nosotros');
  }
  
  // Redireccionar desde '/renders.html' a '/renders'
  if (window.location.pathname === '/renders.html') {
    window.location.replace('/renders');
  }
  
  // Redireccionar desde '/proyectos.html' a '/proyectos'
  if (window.location.pathname === '/proyectos.html') {
    window.location.replace('/proyectos');
  }
  
  // Redireccionar desde '/contacto.html' a '/contacto'
  if (window.location.pathname === '/contacto.html') {
    window.location.replace('/contacto');
  }
  